import React, {useState, useEffect} from 'react';
import { Container } from 'react-bootstrap';
import Articlelist from '../components/Articlelist';
import Banner from '../components/Banner';

function HomePage() {
  const [topbanners, setBanners] = useState([{ 
    name : "",
    title : "",
    bannertext : "",
    pic : ""
  }]);

  const [categories, setCategories] = useState([]);
  const [articleInfo, setarticleInfo] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
        const data = await fetch(`/api/banners`);
        const body = await data.json();
        // console.log(body);
        setBanners(body)
    }
    fetchData();

    const fetchCategories = async () =>{
      const result = await fetch(`/api/categories`);
      const data = await result.json();
      // console.log(data);
      setCategories(data);
    }
    fetchCategories();

    const fetchArticles = async () => {
      const result = await fetch(`/api/articles`);
      const body = await result.json();
      // console.log(body);
      setarticleInfo(body)
    }
    fetchArticles();

  },[]);
  
  return (
    <>
    <Banner banners={topbanners} page="aboutus" />
    <Container fluid={true} className="bg-white pt-5 pb-5">
       <Container>
        {categories.map((category , key) => (
          <Articlelist name={category.name} column={4} key={key} info={articleInfo} id={category._id}/>
        ))}
       </Container>
    </Container>
    </>
  )
}

export default HomePage;