import React,{ useState, useEffect} from 'react';
import { Container } from 'react-bootstrap';
import styled from 'styled-components';
import {Col,Row} from  'react-bootstrap';
import ArticleCard from '../components/ArticleCard';

const Input = styled.input`
    padding: 6px 15px;
    box-shadow: 0px 2px 4px 0px #00000017;
    border-radius:15px;
    margin-bottom: 1.5rem;
    min-width: 400px;
    text-align: center;
    color: #000;
    font-family: 'futuramedium';
    letter-spacing: 1px;
    background:#ffffff;
    border:1px solid transparent;
    transition: all linear 0.6s;
    &:focus{
        border: 1px solid #2da736;
        box-shadow: none;
        outline: none;
    }
`;


function ArticlesListPage() {
  const [articleInfo, setarticleInfo] = useState([]);

  useEffect(() => {
    const fetchArticles = async () => {
      const result = await fetch(`/api/articles`);
      const body = await result.json();
      // console.log(body);
      setarticleInfo(body)
    }
    fetchArticles();   
  }, []);

  return (
    <>
    <Container className="pt-5">
    <Col className="text-center">
      <Input type="text" 
              className="search" 
              placeholder="Search..."
      />
    </Col>
    <Row xs={1} md={4} className="pt-3 pb-5">
      {articleInfo.map((article, key) => (
        <ArticleCard key={key} to ={`/article/${article.name}`} pic={article.banner} title={article.title} content={article.content} />
      ))}
      </Row>
    </Container>
    </>
  )
}

export default ArticlesListPage;