import React from 'react';
import styled from 'styled-components';
import {
        Col,
        Row,
        } from 'react-bootstrap';

import ArticleCard from './ArticleCard';


const CardTitle = styled.h1`
  font-size: 15px;
  text-transform: uppercase;
  margin: 0.7rem 0 0.5rem;
  min-height: 45px;
  font-weight: bold;
  font-family: "Futura-bold";
  color: #626262;
`;

const MainTitle = styled(CardTitle)`
    font-size: 32px;
    color: #000;
    text-transform: capitalize;
    letter-spacing: 1px;
`;



function Articlelist({column, name, info, id}) {
  const filteredArticle = info.filter(obj => {
    return obj.catid === id;
});
  
  return (
    <Row xs={1} md={column} className="pt-3 pb-5">
    {name ? <Col md={12}><MainTitle>{name}</MainTitle></Col> : ""}
       {
         filteredArticle.map((article, key) => (
           <ArticleCard key={key} to ={`/article/${article.name}`} pic={article.banner} title={article.title} content={article.content} />
         ))
       }
     </Row>
  )
}

export default Articlelist;