import React from 'react'

function UpvotesSection({articleName, setArticleInfo, upvotes}) {
    const upvoteArticle = async () => {
        const result = await fetch(`/api/articles/${articleName}/upvote`,{
            method: 'post',
        });

        const body = await result.json();
        setArticleInfo(body);
    }
  return (
    <div className="UpvotesSection" id="upvotes-section">
        <button onClick={() => upvoteArticle()} className="primary btn-primary">Add Upvote</button>
         <p>This Article was upvoted {upvotes} times</p>
    </div>
  )
}

export default UpvotesSection;