import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {
        Card,
        Col,
        } from 'react-bootstrap';
import Image from './Image';

const Kard = styled(Card)`
&:hover{
  box-shadow: 0px 0px 4px rgb(0 0 0 / 25%);
  -webkit-animation: 2s 0.2s fadeInUp both;
  animation: 2s 0.2s fadeInUp both;
}
  margin-bottom:25px; 
  border-radius: 15px;
  padding: 0px;
  overflow: hidden;
`;

const CardTitle = styled.h1`
  font-size: 15px;
  text-transform: uppercase;
  margin: 0.7rem 0 0.5rem;
  min-height: 45px;
  font-weight: bold;
  font-family: "Futura-bold";
  color: #626262;
`;

const Text = styled.p`
  margin-right: 0 !important;
  font-weight: bold;
  line-height: 20px;
  font-size: 15px;
  font-family: 'futuramedium';
  color: #9F9F9F;
`;

const ImageHolders = styled.div`
  display: block;
  overflow: hidden;
  height: 150px;
`;

const Readmore = styled.span`
    font-family: 'futuramedium';
    font-size: 14px;
    letter-spacing: 2px;
    padding: 10px 0;
    display: block;
    color: #2CB34A;
    font-weight: 600;
    text-decoration: none;
    font-weight: bold;

`;

function ArticleCard({to, pic, title, content}) {
  return (
    <>
              <Col>
                <Link to={to} className="article-list-item">
                    <Kard>
                      <ImageHolders>
                      <Image pic={pic} />
                      </ImageHolders>
                      <Card.Body>
                        <CardTitle>{title}</CardTitle>
                        <Text>
                        {content.substring(0 , 120).replace(/\n/g, '')}...
                        </Text>
                        <Readmore>Find Out More</Readmore>
                      </Card.Body>
                  </Kard>
                </Link>
              </Col>
    </>
  )
}

export default ArticleCard;