import React, { useState, useEffect } from 'react';
import { Col, Row, Container, } from 'react-bootstrap';

function AddArticleForm() {
    const [articleTitle, setArticleTitle] = useState('');
    const [articleCategory, setArticleCategory] = useState('');
    const [bannerPic, setBannerPic] = useState('');
    const [content, setContent] = useState('');


    const [categoryData, setCategoryData] = useState([]);
    useEffect(() => {
        const fetchData = async () => {
            const results = await fetch(`/api/categories`);
            const body = await results.json();
            // console.log(body);
            setCategoryData(body);
        }
        fetchData();
    }, []);

    const name = articleTitle.replace(/\s+/g, '-').toLowerCase();
    const addArticle = async () => {
        const result = await fetch(`/api/articles/add-article`,{
            method: 'POST',
            body: JSON.stringify({
                catid: articleCategory,
                name: name,
                title: articleTitle,
                banner: bannerPic,
                content: content
            }),
            headers: {
                'Content-Type': 'application/json'
            },
        });

        const body = await result.json();
        console.log(body);
    }

    return (
        <>
            <Container>
                <Row xs={1} md={2}>
                    <Col md={7} className="mx-auto">
                        <div className="form add-article-form mb-5 mt-5">
                            <h1 className="text-center">Add New Article</h1>
                            <form>
                                <div className="form-group mb-3">
                                    <label>Title</label>
                                    <input type="text" className="form-control"
                                        placeholder="Title" value={articleTitle}
                                        onChange={(e) => setArticleTitle(e.target.value)} />
                                </div>
                                <div className="form-group mb-3">
                                    <label>Select Category</label>
                                    <select className="form-control" defaultValue={articleCategory}
                                        onChange={(e) => setArticleCategory(e.target.value)}>
                                        <option>Select Category</option>
                                        {categoryData.map((category, key) => (
                                            <option value={category._id} key={key}>{category.name}</option>
                                        ))}

                                    </select>
                                </div>
                                <div className="form-group mb-3">
                                    <label>Banner Thumbnail Title</label>
                                    <input type="text" className="form-control" placeholder="Banner Name" value={bannerPic}
                                        onChange={(e) => setBannerPic(e.target.value)} />
                                </div>
                                <div className="form-group mb-3">
                                    <label>Banner Thumbnail Title</label>
                                    <textarea type="text" className="form-control" placeholder="Banner Name" rows={5} cols={80} value={content}
                                        onChange={(e) => setContent(e.target.value)} />
                                </div>
                                <button className="btn btn-primary"
                                onClick={() => addArticle()}
                                >Add Article</button>
                            </form>
                        </div>
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default AddArticleForm;