import React, { useState, useEffect} from 'react';
import { useParams } from 'react-router-dom';
import NotFound from './NotFound';
import styled from 'styled-components';
import Image from '../components/Image';
import Video from '../components/Video';
import {
  Col,
  Row,
  Container,
} 
from 'react-bootstrap';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';
import ArticleList from '../components/Articlelist';

const Title = styled.h1`
  font-weight: bold;
  font-size: 32px;
  margin-bottom: 1rem;
  letter-spacing: 1px;
  font-family: "Futura-bold";
`;

const Line = styled.span`
    display: block;
    width: 1px;
    background: #B9B9B9;
    height: 95%;
    padding-top: 4rem;
    margin: auto;
    position: relative;
    clear: both;
`;


function Articles() {

  let params = useParams();
  const name = params.name;

  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] , video: [], title: "", content: "", banner: "", catid: "" });
  const [others, setOthers] = useState([]);

  // const article = articleInfo.find(article => article.name === name);
  // const article = Array.isArray(articleInfo) ? articleInfo.find(article => article.name === article) : <NotFound />;


  useEffect(() => {

    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      // console.log(body);
      setArticleInfo(body);
    }
    fetchData();

    const fetchArticles = async () => {
      const result = await fetch(`/api/articles`);
      const body = await result.json();
      // console.log(body);
      setOthers(body)
    }
    fetchArticles();

  }, [name]);

  if (!articleInfo) return <NotFound />;
  const filteredArticle = others.filter(obj => {
    return obj.name !== name;
});

  return (
    <>
      <Container className="pt-5">
        <Row xs={1} md={2}>
          <Col md={7}>
            <Image pic={articleInfo.banner} />
            <Title className="pt-4">{articleInfo.title}</Title>
            <UpvotesSection upvotes={articleInfo.upvotes} articleName={name} setArticleInfo={setArticleInfo}/>
              <p>{articleInfo.content}</p>
            <Video videodata={articleInfo.video}/>

            <Row className="mb-5 mt-5 pb-3">
                <CommentsList comments={articleInfo.comments}/>
            </Row>
            <Row className="mb-5 mt-5 pb-3">
              <AddCommentForm articleName={name} setArticleInfo={setArticleInfo}/>
            </Row>
          </Col>
          <Col md={1}>
            <Line />
          </Col>
          <Col md={4} className="related-articles">
            <ArticleList info={filteredArticle} name="Related Article" id={articleInfo.catid} />
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Articles;